import React from 'react';
import logock from '../imgs/ck.png';
import p5 from 'p5';
import 'p5/lib/addons/p5.sound';
import 'p5/lib/addons/p5.dom';

export default class Player extends React.Component {
    constructor(props){
        super(props);

        this.listado = [];
        this.timeTotal = 0;
        this.timeCurrent = 0;
        this.subLoaded = false;
        this.playing = false;
        
        this.state = {
            playing: false
        }

        this.canvas_player = null;
    }

    componentDidMount() {
        const s = ( sketch ) => {

            let x = 100;
            let y = 100;
            let audio, analyzer;

            sketch.setup = () => {
                sketch.createCanvas(0, 0);
                audio = sketch.select('#audioPlayer');
                analyzer = new p5.FFT();
                analyzer.setInput(audio);
            };

            sketch.draw = () => {
                sketch.clear();
                sketch.stroke(150);
                sketch.noFill();

                let spectrum = analyzer.analyze();

                sketch.beginShape();
                for (let i = 0; i < spectrum.length; i+=6) {
                    sketch.vertex(sketch.map(i,0,spectrum.length,0,sketch.width), sketch.map(spectrum[i], 0, 255, 0, sketch.height/2));
                }
                sketch.endShape();

                sketch.beginShape();
                for (let i = spectrum.length; i > 0 ; i-=6) {
                    sketch.vertex(sketch.map(i,0,spectrum.length,sketch.width,0), sketch.map(spectrum[i], 0, 255, sketch.height, sketch.height/2));
                }
                sketch.endShape();
            };

            sketch.resize = (w,h) => {
                sketch.resizeCanvas(w,h);
                console.log('resize');
            }
        };

        this.canvas_player = new p5(s, 'canvas_player');

        

    }
    componentDidUpdate(prevProps, prevState) {
        var width = document.getElementById('canvas_player').offsetWidth;
        var height = document.getElementById('canvas_player').offsetHeight;
        this.canvas_player.resize(width, height);

        if(this.props.url != null && prevProps.url != this.props.url) {
            var audio = document.getElementById("audioPlayer");
            audio.setAttribute('src', this.props.url);

            this.setState({
                playing: false
            })

            audio.oncanplaythrough = () => {
                audio.play();
                this.timeTotal = audio.duration;
                this.setState({
                    playing: true
                })
            }

            audio.ontimeupdate = () => {
                this.timeCurrent = audio.currentTime;
                this.timeTotal = audio.duration;
                this.updateTimeText(this.timeCurrent, this.timeTotal);
                this.updateSubs(this.listado, this.timeTotal, this.subLoaded);
            }

            this.listado = [];
            const urlSubs = "/subtitulos/"+this.props.subtitulos+".json";
            var subsContainer = document.getElementsByClassName("subsContainer")[0];
            var child = subsContainer.lastElementChild;
            while (child) { 
                subsContainer.removeChild(child); 
                child = subsContainer.lastElementChild; 
            }
            
            if(this.props.subtitulos != null){
                fetch(urlSubs)
                .then(res => {
                    return res.json();
                })
                .then(data => {
                    this.subLoaded = false;
                    
                    this.listado = data;
                })
                .catch(err => console.log(err));
            }
        };
    }

    updateTimeText(current, total) {
        var timeTxt = document.getElementsByClassName("time")[0];
        var line = document.getElementsByClassName("current")[0];

        var porcentaje = ((current * 100)/total);

        line.setAttribute('style', 'width: '+porcentaje+'%');
        timeTxt.innerHTML = this.secondsTimeSpanToHMS(current) + "/" + this.secondsTimeSpanToHMS(total);
    }

    updateSubs(subtitulos, total, loaded) {
        if(!this.subLoaded){
            var subsContainer = document.getElementsByClassName("subsContainer")[0];
            for(let i = 0; i < subtitulos.length; i++){
                let node = document.createElement("div");
                var square = document.createElement("span");
                var leftPos = ((subtitulos[i].tiempo * 100)/total);
                node.setAttribute('style', 'left: '+leftPos+'%');
                node.appendChild(square);
                node.setAttribute('data-notice', subtitulos[i].contenido);
                node.addEventListener('click', (e, elem) => {
                    var notice = document.getElementsByClassName("notice")[0];
                    notice.innerHTML = node.getAttribute('data-notice');
                    
                })
                subsContainer.appendChild(node);  
            }

            this.subLoaded = true;
        }

    }

    secondsTimeSpanToHMS(s) {
	    var h = Math.floor(s/3600);
	    s -= h*3600;
	    var m = Math.floor(s/60);
	    s -= m*60;

	    s = Math.floor(s);
	    return (h < 10 ? '0'+h : h)+":"+(m < 10 ? '0'+m : m)+":"+(s < 10 ? '0'+s : s);
	}

    render() {
        let data;

        return (
            <>
                <div className="backImg"></div>
                <div id="canvas_player"></div>
                <audio id="audioPlayer"></audio>
                <img className="relative" src={logock} alt=""/>
                <div className={(this.state.playing) ? 'playerContainer active' : 'playerContainer'}>
                    <div className="notice"></div>
                    <div className="line">
                        <div className="current"></div>
                        <div className="total"></div>
                        <div className="subsContainer"></div>
                    </div>
                    <div className="time relative"></div>
                </div>
            </>
        );
    }
}